<template>
    <el-row class="add-article">
        <el-col :span="20">
            <el-form :model="form" label-width="130px" :rules="rules" ref="editForm">
                <el-form-item label="标题" prop="tittle">
                    <el-input v-model="form.tittle" :maxlength="50" placeholder="新建标题"></el-input>
                </el-form-item>
                <el-form-item label="类型" prop="articleType">
                    <el-select v-model="form.articleType" placeholder="文章类型">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="正文" prop="content">
                    <el-alert
                        title="文章图片规格：封面图（750 * 300）"
                        type="warning"
                        show-icon
                        style="margin-bottom: 10px;line-height: 20px"
                        >
                    </el-alert>
                    <quill-editor
                        class="edit-box"
                        ref="editArea"
                        v-model="form.articleContent"
                        :options="editorOption">
                    </quill-editor>
                </el-form-item>

                <el-form-item class="upload-box" label="封面图：" prop="pic">
                    <el-upload
                      class="avatar-uploader"
                      :action="API + 'media/file_upload'"
                      :show-file-list="false"
                      :before-upload="beforeAvatarUpload"
                      :on-error="onError"
                      :on-success="onSuccess">
                      <img v-if="this.form.articlePicture" :src="this.form.articlePicture" class="avatar">
                      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                    </el-upload>
                    <p class="tips" style="font-size: 12px;color: #9a9a9a;">*封面图上传规格：750*300</p>
                </el-form-item>

                <p class="tittle-bar">推荐车辆：</p>

                <p class="tittle-bar label">车辆ID1：</p>
                <el-form-item class="searchCarNameIpt">
                    <el-input v-model="searchForm1.carName1" :maxlength="30" icon="search" placeholder="查询品牌" @click="searchCarName('searchForm1', 'carID1')"></el-input>
                </el-form-item>
                <el-form-item class="m_l_20" style="display:inline-block" prop="carID1" >
                    <el-select v-model="form.carID1" placeholder="选择推荐车辆ID">
                        <el-option
                            v-for="item in searchForm1.options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                            style="height:50px">
                            <p style="font-size:15px">{{`${item.carName} (ID:${item.value})`}}</p>
                            <p style="font-size:13px;color:#ababab;">{{`${item.carSeries}  ${item.modelYear} ${item.carOwnerName}`}}</p>
                        </el-option>
                    </el-select>
                </el-form-item>
                <br>

                <p class="tittle-bar label">车辆ID2：</p>
                <el-form-item class="searchCarNameIpt">
                    <el-input v-model="searchForm2.carName1" :maxlength="30" icon="search" placeholder="查询品牌" @click="searchCarName('searchForm2', 'carID2')"></el-input>
                </el-form-item>
                <el-form-item class="m_l_20" style="display:inline-block" prop="carID2" >
                    <el-select v-model="form.carID2" placeholder="选择推荐车辆ID">
                        <el-option
                            v-for="item in searchForm2.options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                            style="height:50px">
                            <p style="font-size:15px">{{`${item.carName} (ID:${item.value})`}}</p>
                            <p style="font-size:13px;color:#ababab;">{{`${item.carSeries}  ${item.modelYear} ${item.carOwnerName}`}}</p>
                        </el-option>
                    </el-select>
                </el-form-item>
                <br>


                <p class="tittle-bar label">本期活动车辆ID：</p>
                <el-form-item class="searchCarNameIpt">
                    <el-input v-model="searchForm3.carName1" :maxlength="30" icon="search" placeholder="查询品牌" @click="searchCarName('searchForm3', 'activityCarID')"></el-input>
                </el-form-item>
                <el-form-item class="m_l_20" style="display:inline-block" prop="activityCarID" >
                    <el-select v-model="form.activityCarID" placeholder="本期活动车辆ID">
                        <el-option
                            v-for="item in searchForm3.options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                            style="height:50px">
                            <p style="font-size:15px">{{`${item.carName} (ID:${item.value})`}}</p>
                            <p style="font-size:13px;color:#ababab;">{{`${item.carSeries}  ${item.modelYear} ${item.carOwnerName}`}}</p>
                        </el-option>
                    </el-select>
                </el-form-item>
                <br>

                <el-form-item>
                    <el-button @click="resetForm('editForm')">重置</el-button>
                    <el-button v-if="publishStatus" type="primary" @click="publish('editForm')">发布</el-button>
                    <el-button v-if="!publishStatus" type="primary" @click="editSave('editForm')">保存</el-button>
                </el-form-item>
            </el-form>
        </el-col>
    </el-row>
</template>

<script>
import Quill from 'quill'
export default {
    data() {
        const checkContent = (rule, value, callback) => {
            if (this.form.articleContent == '') {
              callback(new Error('内容不能为空'))
            } else {
              callback()
            }
        }
        const checkPic = (rule, value, callback) => {
            if (!this.form.articlePicture) {
              callback(new Error('请添加成功后重试'))
            } else {
              callback()
            }
        }
        const checkNum = (rule, value, callback) => {
            if (value.trim() === '') {
              callback(new Error('内容不能为空'))
            } else if (isNaN(value)) {
              callback(new Error('请输入数字'))
            } else {
              callback()
            }
        }
        const checkID = (rule, value, callback) => {
            if (value === '') {
              callback(new Error('请选择车辆ID'))
            } else {
              callback()
            }
        }
        return {
            form: {
                tittle: '',
                articleType: '',
                articleContent: '',
                articlePicture: '',
                carID1: '',
                carID2: '',
                activityCarID: ''
            },
            // ID搜索框1
            searchForm1: {
                carName1: '',
                options: '',
            },
            // ID搜索框2
            searchForm2: {
                carName1: '',
                options: '',
            },
            // ID搜索框3
            searchForm3: {
                carName1: '',
                options: '',
            },
            options: [
                {
                  value: '1',
                  label: '超值体验'
                }, {
                  value: '2',
                  label: '发现'
                }
            ],
            editorOption: {
              placeholder: '请输入文本...',
            },
            //发布按钮、保存按钮显示隐藏
            publishStatus: true,
            // 验证规则
            rules: {
                tittle: [
                    {required: true, message: '请填写内容后发布', trigger: 'blur'},
                    {min: 2, message: '大于 2 个字符', trigger: 'blur' }
                ],
                content: [{ validator: checkContent, trigger: 'change' }],
                articleType: [{ required: true, message: '请选择文章类型', trigger: 'change' }],
                pic: [{ validator: checkPic, trigger: 'change' }],
                carID1: [{ required: true, validator: checkID, trigger: 'change'}],
                carID2: [{ required: true, validator: checkID, trigger: 'change'}],
                activityCarID: [{ required: true, validator: checkID, trigger: 'change'}],
            },
            loading: false,
        }
    },
    props: ['API'],
    computed: {
        // 获取id状态
        articleUuid() {
            return this.$store.state.articleUuid
        },
    },
    methods: {
        // 发布
        publish(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认发布?', '提示', {})
                        .then(() => {
                            // this.form.articleContent = this.$common.articleFormat(this.form.articleContent)
                            var json = {
                                "head": this.$common.getUserStorage(),
                                "body": {
                                    "tittle": this.form.tittle,
                                    "articleType": +this.form.articleType,
                                    "articleContent": this.$common.articleFormat(this.form.articleContent),
                                    "articlePicture": this.form.articlePicture,
                                    "carID1": this.form.carID1,
                                    "carID2": this.form.carID2,
                                    "activityCarID": this.form.activityCarID
                                }
                            }
                            this.$common._post('article/newArticle', json, this, data => {
                                this.$message({ type: 'success',message: data.description })
                                this.$router.push('/operating-articles')
                            })
                        }).catch(() => {})
                }
            })
        },
        // 保存
        editSave(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认保存?', '提示', {})
                        .then(() => {
                            this.form.articleContent = this.$common.articleFormat(this.form.articleContent)
                            var json = {
                                "head": this.$common.getUserStorage(),
                                "body": {
                                    "tittle": this.form.tittle,
                                    "articleType": +this.form.articleType,
                                    "articleContent": this.$common.articleFormat(this.form.articleContent),
                                    "articlePicture": this.form.articlePicture,
                                    "articleUuid": this.articleUuid,
                                    "carID1": this.form.carID1,
                                    "carID2": this.form.carID2,
                                    "activityCarID": this.form.activityCarID
                                }
                            }
                            this.$common._post('article/editAndSave', json, this, data => {
                                this.$message({ type: 'success',message: data.description })
                                this.$router.push('/operating-articles')
                            })
                        }).catch(() => {})
                }
            })
        },
        // 查询carID
        searchCarName(searchFormName, formID) {
            this[searchFormName].options = []
            this.form[formID] = ''
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "carName": this[searchFormName].carName1 || this[searchFormName].carName2 || this[searchFormName].carName3
                }
            }
            this.$common._post('article/toSerchCar', json, this, data => {
                this[searchFormName].options = data.list.map(v=>{
                    return {
                        value: v.id || '',
                        label: v.id  || '',
                        carName: v.carName  || '未知车名',
                        brand: v.brand || '未知品牌',
                        modelYear: v.modelYear || '未知年款',
                        carOwnerName: v.carOwnerName || '未知车主',
                        carSeries: v.carSeries || '未知车系',
                    }
                })
            })
        },
        // 重置表单
        resetForm(formName) {
            this.$refs[formName].resetFields()
            this.form = {
                tittle: '',
                articleType: '',
                articleContent: '',
                articlePicture: '',
                carID1: '',
                carID2: '',
                activityCarID: ''
            }
        },
        // 上传之前检查
        beforeAvatarUpload(file) {
            console.log(file)
            // // const isJPG = file.type === 'image/jpeg';
            // const isLt2M = file.size / 1024 / 1024 < 2;

            // // if (!isJPG) {
            //   // this.$message.error('上传头像图片只能是 JPG 格式!');
            // // }
            // if (!isLt2M) {
            //   this.$message.error('上传头像图片大小不能超过 2MB!');
            // }
            // return isJPG && isLt2M;
        },
        // 上传成功
        onSuccess(res, file, fileList) {
            if(res.body.result == 0) {
                this.form.articlePicture = res.body.attachment_url
            }
            this.$message(res.body.description)
        },
        // 上传失败
        onError(res, file, fileList) {
            this.$message(res.body.description)
        },
        // 获取详情
        getDetail(){
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "articleUuid": this.articleUuid
                }
            }
            this.$common._post('article/detail', json, this, data => {
                console.log('getgetgetgetgetgetdata', data)
                this.form = data
                this.form.articleType += ''
            })
        }
    },
    mounted() {
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        
        // 导航高亮
        this.$store.commit('setActiveIndex', 'operating-articles')
        
        // 没有id则不获取详情
        if(this.articleUuid) {
            this.publishStatus = false
            this.getDetail()
        }
    }
}
</script>

<style lang="scss">
    /*上传框*/
    .avatar-uploader .el-upload {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        position: relative;
        overflow: hidden;
      }
  .avatar-uploader .el-upload:hover {
        border-color: #20a0ff;
      }
  .avatar-uploader-icon {
        font-size: 28px;
        color: #8c939d;
        width: 178px;
        height: 178px;
        line-height: 178px;
        border: 1px dashed #d9d9d9;
        text-align: center;
      }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  /*编辑器*/
  .ql-toolbar {
    .ql-formats .ql-picker-label {
        &:before, svg {
            vertical-align: top;
        }

    }
  }
  .ql-container .ql-editor {
    min-height: 400px;
    padding-bottom: 1em;
    max-height: 25em;
  }
  .add-article {
    .searchCarNameIpt {
        display: inline-block;
    }
    .tittle-bar.label {
        font-size:14px;
        margin-bottom:10px;
        display: inline-block;
        width: 130px;
        text-align: right;
    }
  }
</style>



